<template>
  <div
    class="ele-form"
    ref="wrapper"
  >
    <!-- inline -->
    <template v-if="inline">
      <el-form
        :inline="true"
        :label-position="labelPosition || 'right'"
        :label-width="computedLabelWidth"
        :model="formData"
        :rules="rules"
        @submit.native.prevent="handleValidateForm"
        ref="form"
        v-bind="formAttrs"
      >
        <!--  -->
        <slot />
        <slot
          :formData="formData"
          :formDesc="formDesc"
          :formErrorObj="formErrorObj"
          name="form-content"
        >
          <template v-for="(formItem, field) of formDesc">
            <slot :name="field + '-wrapper'">
              <el-form-item
                :error="formErrorObj ? formErrorObj[field] : null"
                :key="field"
                :label="formItem.label"
                :prop="field"
                v-if="formItem.type !== 'hide' && formItem._vif"
              >
                <!--  () -->
                <slot
                  :data="formData[field]"
                  :desc="formItem"
                  :field="field"
                  :formData="formData"
                  :name="field"
                >
                  <component
                    :_disabled="formItem._disabled"
                    :desc="formItem"
                    :is="formItem.type"
                    :options="formItem._options"
                    v-model="formData[field]"
                  />
                </slot>
                <div
                  class="ele-form-tip"
                  v-if="formItem.tip"
                >{{ formItem.tip }}</div>
              </el-form-item>
            </slot>
          </template>
        </slot>
        <!--  -->
        <el-form-item
          class="ele-form-btns"
          style="margin-left: 5px;"
          v-if="btns.length"
        >
          <!--  -->
          <slot
            :btns="btns"
            name="form-btn"
          >
            <el-button
              :key="index"
              @click="btn.click"
              v-bind="btn.attrs"
              v-for="(btn, index) of btns"
            >{{ btn.text }}</el-button>
          </slot>
        </el-form-item>
      </el-form>
    </template>

    <!-- inlinelayout: -->
    <!-- 1.layout labelPosition  span , inline  -->
    <!-- 2.layout form-item , inline , form-itemlayout -->

    <!-- layout -->
    <template v-else>
      <el-row
        justify="center"
        type="flex"
      >
        <el-col :span="formSpan">
          <!--  -->
          <el-form
            :label-position="formLabelPosition"
            :label-width="computedLabelWidth"
            :model="formData"
            :rules="rules"
            @submit.native.prevent="handleValidateForm"
            ref="form"
            v-bind="formAttrs"
          >
            <!--  -->
            <slot />
            <!--  -->
            <slot
              :formData="formData"
              :formDesc="formDesc"
              :formErrorObj="formErrorObj"
              name="form-content"
            >
              <el-row :gutter="20">
                <template v-for="(formItem, field) of formDesc">
                  <slot :name="field + '-wrapper'">
                    <el-col
                      :key="field"
                      :md="formItem.layout || 24"
                      :xs="24"
                      v-if="formItem.type !== 'hide' && formItem._vif"
                      v-show="formItem._vshow"
                    >
                      <el-form-item
                        :error="formErrorObj ? formErrorObj[field] : null"
                        :label="formItem.label"
                        :prop="field"
                      >
                        <!--  () -->
                        <slot
                          :data="formData[field]"
                          :desc="formItem"
                          :field="field"
                          :formData="formData"
                          :name="field"
                        >
                          <component
                            :_disabled="formItem._disabled"
                            :desc="formItem"
                            :is="formItem.type"
                            :options="formItem._options"
                            v-model="formData[field]"
                          />
                        </slot>
                        <div
                          class="ele-form-tip"
                          v-if="formItem.tip"
                        >{{ formItem.tip }}</div>
                      </el-form-item>
                    </el-col>
                  </slot>
                </template>
              </el-row>
            </slot>
            <!--  -->
            <el-form-item
              class="ele-form-btns"
              v-if="btns.length"
            >
              <!--  -->
              <slot
                :btns="btns"
                name="form-btn"
              >
                <el-button
                  :key="index"
                  @click="btn.click"
                  v-bind="btn.attrs"
                  v-for="(btn, index) of btns"
                >{{ btn.text }}</el-button>
              </slot>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </template>
  </div>
</template>

<script>
import responsiveMixin from './mixins/responsiveMixin'
import utils from './tools/utils'
import { throttle } from 'throttle-debounce'
import localeMixin from 'element-ui/src/mixins/locale'
import { t } from './locale'
import { equal, intersection } from './tools/set'
const cloneDeep = require('lodash.clonedeep')

export default {
  name: 'EleForm',
  // mixin,  responsiveMixin 
  mixins: [responsiveMixin, localeMixin],
  props: {
    // 
    formDesc: {
      type: Object,
      required: true
    },
    // 
    formData: {
      type: Object,
      required: true
    },
    // 
    inline: {
      type: Boolean,
      default: false
    },
    // 
    formAttrs: Object,
    // 
    rules: Object,
    // 
    isLoading: {
      type: Boolean,
      default: false
    },
    // 
    formError: Object,
    // 
    requestFn: Function,
    // 
    formBtns: Array,
    // 
    formBtnSize: String,
    // submit
    isShowSubmitBtn: {
      type: Boolean,
      default: true
    },
    //  cancel 
    isShowCancelBtn: {
      type: Boolean,
      default: false
    },
    // back
    // :  inline true, ; inline  false, . : computedIsShowBackBtn
    isShowBackBtn: {
      type: Boolean,
      default: null
    },
    // reset
    isShowResetBtn: {
      type: Boolean,
      default: false
    },
    // 
    // :  inline true,  ''; inline  false ,   ''. : computedSubmitBtnText
    submitBtnText: {
      type: String,
      default: null
    },
    // 
    backBtnText: String,
    // 
    resetBtnText: String,
    // 
    cancelBtnText: String,
    // 
    labelWidth: {
      type: [Number, String],
      default: 'auto'
    },
    // (layout, inline)
    labelPosition: String,
    // , (layout, inline)
    span: Number
  },
  data () {
    return {
      // 
      formLabelPosition: 'right',
      // 
      formSpan: 22,
      // 
      innerIsLoading: false,
      // 
      innerFormError: {}
    }
  },
  computed: {
    // 
    btns () {
      const formBtnSize = this.formBtnSize
      let btns = []
      // 
      if (this.isShowSubmitBtn) {
        btns.push({
          attrs: {
            type: 'primary',
            size: formBtnSize,
            loading: this.isLoading || this.innerIsLoading,
            'native-type': 'submit'
          },
          text: this.computedSubmitBtnText,
          click () { }
        })
      }

      // 
      if (this.formBtns) {
        const customBtns = this.formBtns.map(btn => ({
          attrs: {
            type: btn.type,
            size: formBtnSize
          },
          text: btn.text,
          click: btn.click
        }))
        btns = [...btns, ...customBtns]
      }

      // 
      if (this.computedIsShowBackBtn) {
        btns.push({
          attrs: {
            size: formBtnSize
          },
          text: this.backBtnText || t('ele-form.backBtnText'),
          click: this.goBack
        })
      }

      // 
      if (this.isShowCancelBtn) {
        btns.push({
          attrs: {
            size: formBtnSize
          },
          text: this.cancelBtnText || t('ele-form.cancelBtnText'),
          click: this.handleCancelClick
        })
      }

      // 
      if (this.isShowResetBtn) {
        btns.push({
          attrs: {
            size: formBtnSize
          },
          text: this.resetBtnText || t('ele-form.resetBtnText'),
          click: this.resetForm
        })
      }
      return btns
    },
    // (inlinelayout)
    computedIsShowBackBtn () {
      if (utils.is(this.isShowBackBtn, 'Boolean')) {
        return this.isShowBackBtn
      } else {
        return !this.inline
      }
    },
    // (inlinelayout)
    computedSubmitBtnText () {
      if (utils.is(this.submitBtnText, 'String')) {
        return this.submitBtnText
      } else {
        return this.inline
          ? t('ele-form.submitBtnTextInline')
          : t('ele-form.submitBtnText')
      }
    },
    // ()
    computedLabelWidth () {
      if (isNaN(Number(this.labelWidth))) {
        return this.labelWidth
      } else {
        return this.labelWidth + 'px'
      }
    },
    // 
    formErrorObj () {
      return Object.assign({}, this.innerFormError, this.formError)
    }
  },
  watch: {
    // options
    formDesc: {
      handler (desc) {
        if (desc) {
          Object.keys(desc).forEach(field => {
            //  type
            desc[field].type = this.getComponentName(desc[field].type)

            //  options
            this.changeOptions(desc[field].options, field)
          })

          // 
          this.checkLinkage()
        }
      },
      immediate: true
    },
    formErrorObj (obj) {
      // 
      if (obj) {
        this.processError(obj)
      }
    },
    formData: {
      handler (formData) {
        if (formData) {
          // 
          this.checkLinkage()
        }
      },
      deep: true
    }
  },
  methods: {
    // 
    checkLinkage () {
      if (this.checkVifFn) {
        this.checkLinkageFn()
      } else {
        this.checkLinkageFn = throttle(300, () => {
          const formDesc = this.formDesc
          const formData = this.formData
          Object.keys(formDesc).forEach(field => {
            const formItem = formDesc[field]
            // 1. v-if  / 
            if (typeof formItem.vif === 'function') {
              const vif = Boolean(formItem.vif(formData))
              this.formDesc[field]._vif = vif

              if (!vif) {
                // , 
                this.formData[field] = null
              }
            } else if (typeof formItem.vif === 'boolean') {
              this.formDesc[field]._vif = formItem.vif
            } else {
              this.formDesc[field]._vif = true
            }

            // 2. v-show  / 
            if (typeof formItem.vshow === 'function') {
              const vshow = Boolean(formItem.vshow(formData))
              this.formDesc[field]._vshow = vshow
            } else if (typeof formItem.vshow === 'boolean') {
              this.formDesc[field]._vshow = formItem._vshow
            } else {
              this.formDesc[field]._vshow = true
            }

            // 3. disabled  / 
            if (typeof formItem.disabled === 'function') {
              this.formDesc[field]._disabled = formItem.disabled(formData)
            } else if (typeof formItem.disabled === 'boolean') {
              this.formDesc[field]._disabled = formItem.disabled
            }

            // 4. options
            if (
              formItem._vif &&
              formItem.isReloadOptions &&
              typeof formItem.options === 'function'
            ) {
              this.changeOptions(formItem.options, field, true)
            }
          })
        })
        this.checkLinkageFn()
      }
    },
    // 
    getComponentName (type) {
      if (this.$EleFormBuiltInNames.includes(type)) {
        // 
        return 'ele-form-' + type
      } else {
        // 
        return type
      }
    },
    // options
    getObjArrOptions (options) {
      return options.map(option => {
        if (utils.is(option, ['Number', 'String', 'Boolean'])) {
          //  ['', ''] => [ { text: '', value: '' }, { text: '', value: '' } ]
          return {
            text: option,
            value: option
          }
        } else {
          //  
          return option
        }
      })
    },
    // : , , Promise 
    changeOptions (options, field, resetValue = false) {
      if (options) {
        if (options instanceof Array) {
          // options: 
          this.setOptions(options, field, resetValue)
        } else if (options instanceof Function) {
          // options: 
          this.changeOptions(options(this.formData), field, resetValue)
        } else if (options instanceof Promise) {
          // optionsPromise: Promise, 
          options.then(options => {
            this.changeOptions(options, field, resetValue)
          })
        } else {
          // 
          throw new TypeError(
            'options, optionsoptions: , , Promise, : ' +
            options +
            ', '
          )
        }
      } else {
        if (this.formDesc[field]._options) {
          // new optionsnull / undefined,  old Options , 
          this.setOptions([], field, resetValue)
        }
      }
    },
    // options
    setOptions (options, field, resetValue) {
      const newOptions = this.getObjArrOptions(options)
      const oldOptions = this.formDesc[field]._options
      this.formDesc[field] = Object.assign({}, this.formDesc[field], {
        _options: newOptions
      })

      // 
      if (resetValue && oldOptions !== undefined) {
        const newOptionValues = new Set(Array.isArray(newOptions) ? newOptions.map((item) => item.value) : [])
        const oldOptionValues = new Set(Array.isArray(oldOptions) ? oldOptions.map((item) => item.value) : [])

        // 1. & 2. oldOptions  newOptions ,  value 
        const isIntersection = intersection(newOptionValues, oldOptionValues).size
        const isEqual = equal(newOptionValues, oldOptionValues)

        if (!isIntersection && !isEqual) {
          this.formData[field] = null
        }
      }
    },
    // 
    handleValidateForm () {
      if (this.rules) {
        // 
        this.$refs['form'].validate((valid, invalidFields) => {
          if (valid) {
            // 
            this.handleSubmitForm()
          } else {
            // 
            this.processError(invalidFields)
          }
        })
      } else {
        // 
        this.handleSubmitForm()
      }
    },

    // 
    processError (errObj) {
      try {
        const messageArr = Object.keys(errObj).reduce((acc, key) => {
          const formItem = this.formDesc[key]
          const label =
            formItem && formItem.label ? formItem.label + ': ' : key + ': '
          if (errObj[key] instanceof Array) {
            // errorObj: { name: [ { filed: 'name',  message: 'name is required' }] }
            // 
            errObj[key].forEach(item => {
              acc.push(label + item.message)
            })
          } else {
            // errorObj: { name: 'name is required' }
            // 
            acc.push(label + errObj[key])
          }

          return acc
        }, [])
        this.showError(messageArr)
        // eslint-disable-next-line
      } catch { }
    },

    // 
    showError (messageArr) {
      if (messageArr.length) {
        const h = this.$createElement
        messageArr = messageArr.map(msg => {
          return h('div', { style: { marginBottom: '8px' } }, msg)
        })
        this.$notify.error({
          title: t('ele-form.formError'),
          message: h(
            'div',
            { style: { minWidth: '300px', marginTop: '12px' } },
            messageArr
          )
        })
      }
    },

    // 
    async handleSubmitForm () {
      // ,  clone
      let data = cloneDeep(this.formData)
      // valueFormatter
      for (const field in data) {
        const formItem = this.formDesc[field]
        if (formItem && formItem.valueFormatter) {
          data[field] = formItem.valueFormatter(data[field], data)
        }
      }

      if (this.requestFn) {
        // 
        if (this.innerIsLoading) return
        this.innerIsLoading = true
        try {
          const response = await this.requestFn(data)
          this.$nextTick(() => {
            this.$emit('request-success', response)
          })
        } catch (error) {
          // 
          if (error instanceof Error) {
            // Error, 
            try {
              const msg = JSON.parse(error.message)
              if (msg instanceof Object) {
                this.innerFormError = msg
              }
              // eslint-disable-next-line
            } catch { }
          } else if (error instanceof Object) {
            // , 
            this.innerFormError = error
          }
          this.$emit('request-error')
        } finally {
          this.innerIsLoading = false
          this.$emit('request-end')
        }
      } else {
        // 
        if (this.isLoading) return
        this.$emit('request', data)
      }
    },
    // 
    goBack () {
      if (this.$router) {
        // vue-router
        this.$router.back()
      } else {
        // history API
        history.back()
      }
    },
    // 
    handleCancelClick () {
      this.$emit('close')
    },
    // 
    resetForm () {
      this.$refs.form.resetFields()

      // 
      this.$refs.form.fields.forEach(field => {
        this.formData[field.prop] = field.initialValue
      })
    }
  }
}
</script>

<style>
.ele-form-tip {
  color: #909399;
  line-height: 1.5em;
  margin-top: 3px;
}

.ele-form-full-line.el-date-editor.el-input,
.el-date-editor.el-input__inner,
.ele-form-full-line.el-date-editor--daterange.el-input__inner,
.ele-form-full-line.el-date-editor--datetimerange.el-input__inner {
  width: 100%;
}
.ele-form-full-line.el-cascader,
.ele-form-full-line.el-select,
.ele-form-full-line.el-autocomplete {
  width: 100%;
}
</style>
